@import url(//at.alicdn.com/t/font_1152412_eeegcwi77ju.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
div,p,ul,li{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.header {
  background: rgba(0, 0, 0, 0);
  position: absolute;
  top: 0;
  width: 100%;
  color: #fff;
  z-index: 199;
  padding: 10px; }
  .header i {
    font-size: 28px;
    float: right; }

.opened-header {
  background: rgba(8, 8, 8, 0.9); }

.menu-wrapper {
  width: 100%;
  text-align: center;
  clear: both; }
  .menu-wrapper li {
    list-style: none;
    width: 67%;
    margin: 0 auto;
    height: 45px;
    line-height: 45px;
    border-bottom: 0.5px solid rgba(200, 200, 200, 0.9); }
    .menu-wrapper li a {
      color: #fff;
      text-decoration: none; }

.index-page {
  width: 100%;
  min-height: 100%; }

.content-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  min-height: 100%;
  font-size: 8px;
  background-color: #080808;
  color: #fff; }
  .content-wrapper .image {
    width: 100%; }

.section {
  font-size: 1rem;
  padding: 20px; }
  .section .main-num {
    font-size: 1.8rem; }
  .section .second-num {
    font-size: 0.9rem; }
    .section .second-num Text {
      font-size: 0.6rem;
      margin-left: 10px; }

.temperature-section {
  position: relative;
  height: 100vh;
  background-color: #080808;
  background-image: url("https://wpimg.wallstcn.com/c07e14e2-2ae8-4110-92b9-f174356226c8.png");
  background-size: contain;
  background-position: right;
  background-repeat: no-repeat;
  color: #fff; }
  .temperature-section .temperature-num {
    font-size: 56px;
    text-align: left;
    margin-top: 15px; }
    .temperature-section .temperature-num .main-num {
      font-size: 2.8rem; }
      .temperature-section .temperature-num .main-num .sm-text {
        font-size: 0.6rem;
        margin-left: 10px; }
    .temperature-section .temperature-num .second-num {
      margin-bottom: 10px; }
  .temperature-section .location-wrapper {
    position: absolute;
    bottom: 100px;
    right: 30px; }
    .temperature-section .location-wrapper .location-date {
      font-size: 1.5rem; }
    .temperature-section .location-wrapper .location {
      font-size: 0.7rem; }
      .temperature-section .location-wrapper .location:last-child {
        font-size: 0.6rem; }

.wd-section {
  display: -webkit-flex;
  display: flex; }
  .wd-section div {
    width: 33%; }

.about-page {
  width: 100%;
  height: 100vh;
  background-image: url("https://wpimg.wallstcn.com/4b3dce1b-0693-4437-a096-38295ece2a4b.jpg");
  background-size: 100% 100%;
  background-color: #000;
  color: #fff;
  text-align: center;
  padding-top: 80px; }
  .about-page div .about-header {
    color: #fff;
    -webkit-text-stroke: #000;
    font-size: 1.6rem;
    margin: 20px 0; }
  .about-page div div {
    font-size: 0.8rem;
    line-height: 2; }
  .about-page div .thanks-header {
    color: #fff;
    font-size: 1.2rem;
    margin-bottom: 10px; }
  .about-page .about-section {
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 10px;
    padding: 20px;
    margin: 0 auto;
    width: 85%; }

