.about-page {
    width: 100%;
    height: 100vh;
    background-image: url('https://wpimg.wallstcn.com/4b3dce1b-0693-4437-a096-38295ece2a4b.jpg');
    // background-repeat: no-repeat;
    background-size: 100% 100%;
    background-color: #000;
    color: #fff;
    text-align: center;
    padding-top: 80px;
    div .about-header {
        color: #fff;
        -webkit-text-stroke: #000;
        font-size: 1.6rem;
        margin: 20px 0;
    }
    
    div div {
        font-size: 0.8rem;
        line-height: 2;
    }
    
    div .thanks-header {
        color: #fff;
        font-size: 1.2rem;
        margin-bottom: 10px;
    }
    .about-section {
        background-color: rgba(0,0,0, 0.4);
        border-radius: 10px;
        padding: 20px;
        margin: 0 auto;
        width: 85%;
    }
}